import Vue from 'vue'
import Vuex from 'vuex'
import img1 from '../src/static/images/20190104_190104_0027.jpg'
import img2 from '../src/static/images/20190104_190104_0028.jpg'
import img3 from '../src/static/images/20190104_190104_0029.jpg'
import img4 from '../src/static/images/20190104_190104_0030.jpg'
import img5 from '../src/static/images/20190104_190104_0031.jpg'

Vue.use(Vuex)
const NavigatorLang = (navigator.language || navigator.userLanguage).substr(0, 2)
export default new Vuex.Store({
  state: {
    isLoading: false,
    good_list: [{
        id: "1",
        name: "",
        enname: "Handmade green tea",
        content: "The amount of tea used in the bubble-yang yang tip can vary from person to person, but not too little. In general, tea is used in proportion to three-point tea seven-point water or four-point tea six-point water. The meaning of this ratio means that after the tea is brewed, the proportion of tea in the cup accounts for seven points of the three-point water or the proportion of the tea in the cup accounts for six points of the four-point water. If you drink strong tea, you can increase the amount of tea used.",
        zhcontent: "",
        price: 9.9,
        delprice: 20,
        img: img1,
        count: 1,
        icon: 1,
      },
      {
        id: "2",
        name: "",
        enname: "Handmade bayberry",
        content: "Regarding the problem of de-worming of red bayberry, the time of salt water bubble is different. I chose to pick wild wild bayberry. After screening, there are very few insects. I soaked it for more than 30 minutes for sanitation or light salt water. In addition, I also added a step to boil. To clean. Regarding the manual problem of enucleation, the selected juice of Yangmei is not particularly rich, so it is helpless to use the juicer. After cooking and softening, the pieces are manually removed with wooden spoons. It is completely handmade. Please weigh the method.",
        zhcontent: '30       ',
        price: 12.8,
        delprice: 0,
        img: img2,
        count: 1,
        icon: 3
      },
      {
        id: "3",
        name: "",
        enname: "Handmade bayberry",
        content: "Its time for Yangmei. The red and sweet arbutus is mouth watering. However, there are videos on the Internet that Yangmei is glued, and there is a saying that Yangmeili is a bug, and eating a bayberry will eat 10 bugs. The washing of the red bayberry is due to dyeing. This made many people stunned. Can you still eat bayberry?",
        zhcontent: '10',
        price: 3.9,
        delprice: 10,
        img: img3,
        count: 1,
        icon: 2
      },
      {
        id: "4",
        name: "",
        enname: "Handmade with side plum",
        content: `The old Suzhou handmade Jiayingzi is famous overseas. People say that the Suhang girl is the most beautiful, but the Suzhou Jiayingzi is sweeter and sweeter than the Suzhou girl.
Jiayingzi is a kind of candied fruit, originated from the Quanzhou and Zhangzhou areas of Fujian. It is characterized by sweet and fragrant taste and rich aftertaste. In 1958, Fuzhou candied fruit plant based on foreign "Ximei" samples, using local hibiscus Ligan and white granulated sugar as raw materials, using precious Chinese medicine as a flavoring agent, using vacuum concentrated cooking, atmospheric pressure modulation, multiple sugar infiltration It is made from many seasonings.`,
        zhcontent: '     1958           ',
        price: 5.6,
        delprice: 12,
        img: img4,
        count: 1,
        icon: 3
      }, {
        id: "5",
        name: "",
        enname: "Handmade prunes",
        content: "Avoid direct sunlight, store in a cool place, open the bag or eat in the refrigeratorOld Shanghai handmade prunesPrunes are rich in nutrients, including cellulose, vitamin A, minerals, and trace elements.Experts have studied that eating three medium - sized prune is equivalent to eating the fiber content of a fresh fruit.Cellulose A healthy adult should consume 25 to 35 grams of cellulose per day.Eating three medium - sized prunes can be equivalent to a fresh fruit.        ",
        zhcontent: `A2535`,
        price: 7.6,
        delprice: 13.5,
        img: img5,
        count: 1,
        icon: 2
      }
    ],
    addcart: [],
    currentLang: localStorage.getItem('UserLang') || NavigatorLang,
    goodsList: localStorage["goodsList"] ? JSON.parse(localStorage["goodsList"]) : []
  },
  mutations: {
    LOADING(state, status) {
      state.isLoading = status
    },
    UPDATELANG(state, value) {
      state.currentLang = value
      localStorage.setItem('UserLang', state.currentLang)
    },
    //
    // ADD_TO_CART(state, {
    //   id
    // }) {
    //   let record = state.addcart.find(item => item.id == id);
    //   if (!record) {
    //     state.addcart.push({
    //       id,
    //       num: 1
    //     })
    //   } else {
    //     record.num++
    //   }
    // },

    ADD_TO_CART(state, {
      id
    }) {
      let list = state.goodsList.find(item => item.id == id);

      if (!list) {
        state.goodsList.push({
          id,
          num: 1
        });
      } else {
        list.num++
      }
      localStorage.setItem("goodsList", JSON.stringify(state.goodsList));
    },

    ADD_TO_CART_PAGE(state, {
      id,
      num
    }, ) {
      let list = state.goodsList.find(item => item.id == id);
      if (!list) {
        state.goodsList.push({
          id,
          num: num
        });
      } else {
        list.num += num
      }
      localStorage.setItem("goodsList", JSON.stringify(state.goodsList));
    },

    //
    // NUM_CHANGE(state, {
    //   id,
    //   value
    // }) {
    //   state.addcart.forEach((item, index) => {
    //     if (item.id == id) {
    //       item.num = value;
    //     }
    //   });
    // },

    NUM_CHANGE(state, {
      id,
      value,
    }) {
      state.goodsList.forEach((item, index) => {
        if (item.id == id) {
          item.num = value;
        }
      });
    },

    //
    // DELETE(state, product) {
    //   //console.info(state,product)
    //   state.addcart.forEach((item, index) => {
    //     if (item.id == product.id) {
    //       //console.info( item )
    //       //index
    //       state.addcart.splice(index, 1);
    //     }
    //   })
    // },
    DELETE(state, product) {
      //console.info(state,product)
      state.goodsList.forEach((item, index) => {
        if (item.id == product.id) {
          //console.info( item )
          //index
          state.goodsList.splice(index, 1);
          localStorage.setItem("goodsList", JSON.stringify(state.goodsList));
        }
      })
    },

    //
    CLEAR(state) {
      state.addcart = []
    }
  },

  actions: {
    updateLoading(context, status) {
      context.commit('LOADING', status)
    },
    updateLang(context, status) {
      context.commit('UPDATELANG', status)
    },

    addToCartPage({
      commit
    }, product, num) {
      commit('ADD_TO_CART_PAGE', {
        id: product.id,
        num: product.num
      }, )
    },

    addToCart({
      commit
    }, product) {
      commit('ADD_TO_CART', {
        id: product.id,
      }, )
    },

    numChange({
      commit
    }, data) {
      commit('NUM_CHANGE', {
        id: data.id,
        value: data.value,
      })
    },

    //
    delProduct({
      commit
    }, product) {
      commit('DELETE', product)
    },

    //
    clearAllCart({
      commit
    }) {
      commit('CLEAR')
    }
  },
  getters: {
    getLang: state => {
      return state.currentLang;
    },
    //
    goodList: state => {
      return state.good_list.map(item => {
        return {
          id: item.id,
          name: item.name,
          enname: item.enname,
          content: item.content,
          zhcontent: item.zhcontent,
          price: item.price.toFixed(2),
          delprice: item.delprice.toFixed(2),
          img: item.img,
          count: item.count,
          icon: item.icon,
        }
      })
    },
    //
    cartProducts: state => {
      return state.addcart.map(({
        id,
        num
      }) => {
        let product = state.good_list.find(item => item.id == id)
        // console.info('product',product)
        return {
          id: product.id,
          name: product.name,
          price: product.price.toFixed(2),
          delprice: product.delprice.toFixed(2),
          img: product.img,
          count: product.count,
          num,
          total_num: (product.price * num).toFixed(2),
          icon: product.icon,
        }
      })
    },

    //local
    cartProductsList: state => {
      return state.goodsList.map(({
        id,
        num
      }) => {
        let product = state.good_list.find(item => item.id == id)
        // console.info('product',product)
        return {
          id: product.id,
          name: product.name,
          enname: product.enname,
          price: product.price.toFixed(2),
          delprice: product.delprice.toFixed(2),
          img: product.img,
          count: product.count,
          num,
          total_num: (product.price * num).toFixed(2),
          icon: product.icon,
        }
      })
    },

    //
    totalPrice: (state, getters) => {
      let total = 0;
      getters.cartProductsList.forEach(item => {
        total += item.price * item.num
      })
      return total.toFixed(2);
    },

    //
    totalNum: (state, getters) => {
      let total = 0;
      getters.cartProductsList.forEach(item => {
        total += item.num
      })
      return total;
    },
    //local
    totalNumList: (state, getters) => {
      let total = 0;
      getters.cartProductsList.forEach(item => {
        total += item.num
      })
      return total;
    },

  }
})
